.App .form button {
  background-color: #0B7B3E;
  font-family: Monopol, serif;
  color: #FED401;
  padding: 10px;
  font-size: 2.5em;
  margin: 10px;
  margin-right: 0px;
  border: 2px solid #FED401;
  width: 100%;
}
.App .form button:hover {
  background-color: #FED401;
  border: 2px solid #0B7B3E;
  color: #0B7B3E;
  cursor: pointer;
}

.App .list li {
  color: #333;
  background-color: white;
  color: #444;
  padding: 15px;
  margin-right: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  -webkit-transition: background-color .4s ease-out;
  -o-transition: background-color .4s ease-out;
  transition: background-color .4s ease-out;
  list-style: none;
}

.App .list li:hover {
  background-color: #0B7B3E;
  cursor: pointer;
}

.App .mapWrapper {
  width: 100%;
  margin: 10px;
  margin-right: 0px;
}


